/*


*/
var moment = require('moment');

var Parser = require('../parser').Parser;

var ParsedResult = require('../../result').ParsedResult;

var util = require('../../utils/ZH-Hant.js');

var PATTERN = new RegExp('(\\d+|[' + Object.keys(util.NUMBER).join('') + ']+||)(?:\\s*)' + '(?:)?' + '((?:)?|||||||||)' + '(?:(?:|)?|(?:)?)', 'i');
var NUMBER_GROUP = 1;
var UNIT_GROUP = 2;

exports.Parser = function ZHHantCasualDateParser() {
  Parser.apply(this, arguments);

  this.pattern = function () {
    return PATTERN;
  };

  this.extract = function (text, ref, match, opt) {
    var index = match.index;
    text = match[0];
    var result = new ParsedResult({
      index: index,
      text: text,
      ref: ref
    });
    var number = parseInt(match[NUMBER_GROUP]);

    if (isNaN(number)) {
      number = util.zhStringToNumber(match[NUMBER_GROUP]);
    }

    if (isNaN(number)) {
      var string = match[NUMBER_GROUP];

      if (string === '') {
        number = 3;
      } else if (string === '') {
        number = 0.5;
      } else {
        //just in case
        return null;
      }
    }

    var date = moment(ref);
    var unit = match[UNIT_GROUP];
    var unitAbbr = unit[0];

    if (unitAbbr.match(/[]/)) {
      if (unitAbbr == '' || unitAbbr == '') {
        date.add(number, 'd');
      } else if (unitAbbr == '' || unitAbbr == '') {
        date.add(number * 7, 'd');
      } else if (unitAbbr == '') {
        date.add(number, 'month');
      } else if (unitAbbr == '') {
        date.add(number, 'year');
      }

      result.start.assign('year', date.year());
      result.start.assign('month', date.month() + 1);
      result.start.assign('day', date.date());
      return result;
    }

    if (unitAbbr == '') {
      date.add(number, 'second');
    } else if (unitAbbr == '') {
      date.add(number, 'minute');
    } else if (unitAbbr == '' || unitAbbr == '') {
      date.add(number, 'hour');
    }

    result.start.imply('year', date.year());
    result.start.imply('month', date.month() + 1);
    result.start.imply('day', date.date());
    result.start.assign('hour', date.hour());
    result.start.assign('minute', date.minute());
    result.start.assign('second', date.second());
    result.tags.ZHHantDeadlineFormatParser = true;
    return result;
  };
};