import { InputRule } from "./inputrules"; // :: InputRule Converts double dashes to an emdash.

export var emDash = new InputRule(/--$/, ""); // :: InputRule Converts three dots to an ellipsis character.

export var ellipsis = new InputRule(/\.\.\.$/, ""); // :: InputRule Smart opening double quotes.

export var openDoubleQuote = new InputRule(/(?:^|[\s\{\[\(\<'"\u2018\u201C])(")$/, ""); // :: InputRule Smart closing double quotes.

export var closeDoubleQuote = new InputRule(/"$/, ""); // :: InputRule Smart opening single quotes.

export var openSingleQuote = new InputRule(/(?:^|[\s\{\[\(\<'"\u2018\u201C])(')$/, ""); // :: InputRule Smart closing single quotes.

export var closeSingleQuote = new InputRule(/'$/, ""); // :: [InputRule] Smart-quote related input rules.

export var smartQuotes = [openDoubleQuote, closeDoubleQuote, openSingleQuote, closeSingleQuote];